const AddLettersRegistered = (currentKey, lastLetterRegistered, setLastLetterRegistered) => {

    if (!lastLetterRegistered.includes(currentKey.toUpperCase())) {
        
        setLastLetterRegistered([...lastLetterRegistered, currentKey.toUpperCase()])
        console.log(lastLetterRegistered)
    }
}

export default AddLettersRegistered
