import React, {useState} from 'react'
import Register_Input from '../../Scripts/Register input'
import SelectRandomWord from '../../Scripts/SelectRandomWord'
import BringTheWords from './Firebase Querys/BringTheWords'

const PuzzleWord = ({hangmanFrame, setHangmanFrame, currentScore, setCurrentScore, setIsVictory, setIsDefeat, displayApp, setDisplayApp, language, category, categoryIsReady, lettersRegistered, setLettersRegistered}) => {

    const [actualWord, setActualWord] = useState('')

    React.useEffect(() => {

        const generatePuzzleWord = () => {  
            
            const puzzleWord = document.getElementById('puzzleWord')
            
            for (let i = 0; i < actualWord.length; i++) {
                
                let letter = document.createElement('span')
                
                letter.className = 'letter'
                letter.textContent = ''
                
                puzzleWord.appendChild(letter)
            }
        
            const counter = document.createElement('span')
            counter.className = 'counter'
            counter.textContent = '(' + actualWord.length + ')'
        
            puzzleWord.appendChild(counter)
        }

        
        
        if (actualWord && !displayApp) {

            generatePuzzleWord()
            setDisplayApp(true)
    
        }
        
    }, [actualWord, category, displayApp, language, setDisplayApp])


    React.useEffect(() => {

        const definePuzzle = async () => {
        
            const words = await BringTheWords(language, category)
            const wordSelection = await SelectRandomWord(words)
            
            const word = await words[wordSelection]
    
            setActualWord(word)

        }
        
        if (!displayApp && !actualWord) {

            if ( categoryIsReady) {
                
                definePuzzle()
            }
        }


        if (displayApp) {
            console.log(lettersRegistered);

            Register_Input(actualWord, hangmanFrame, setHangmanFrame, setIsVictory, setIsDefeat, lettersRegistered, setLettersRegistered)
        }


    }, [hangmanFrame, displayApp, actualWord, setHangmanFrame, setIsDefeat, setIsVictory, categoryIsReady, category, lettersRegistered, setLettersRegistered, language])
    
    
   
    
    return (
        <>
            <div className="puzzleWord" id="puzzleWord"></div>
        </>
    )
}

export default PuzzleWord
